import React, {useState} from "react";
import {Modal, Button, Card, Accordion} from "react-bootstrap";
import "./Tutorial.css"
import PickAlgorithmImage from './PickAgorithms.png';
import SudokuLogo from './SudokuLogo.png';
import SudokuGif from './mySudokuGif.gif';
import deleteGif from './deleteGif.gif';
import navBarPicture from './navBar.png';
import loneNumberEx from './loneNumberEx.png'
import disjointChainEx from './disjointChainEx.png'
import nakedSubsetEx from './nakedSubsetEx.png'
import disjointChainSolved from './disjointChainSolved.png'
import nakedSubsetSolved from './nakedSubsetSolved.png'

export default function Tutorial(props) {


    

    const titles = ["Welcome to Sudoku Solver Visualizer!", 
                    "Play the game", 
                    "Delete numbers as you want", 
                    "Pick an Algo", 
                    "Informations about Algorithms",
                    "The heuristics", 
                    "Other fonctionality",
                    "Have Fun! "]

    const bodyText = 
        [   
            //Page 1
            <div>
                <Modal.Body>
                    In this Tutorial, I will present yout the main features of the app! 
                </Modal.Body>
                <Modal.Body>
                    Also, if you don't want to read the tuto, feel free to skip it!
                </Modal.Body>
                <img 
                    src = {SudokuLogo}
                    alt="description of image"
                    className = "center"
                    style = {{width: "30%"}}
                />
            </div>,

            //Page 2
            <div>
                <Modal.Body>
                    You can play the game if you want to.
                </Modal.Body>
                <Modal.Body>
                    To fill the grid, choose a number, and drag it in the cell it belongs to!
                </Modal.Body>
                <Modal.Body>
                    <img 
                        src = {SudokuGif}
                        alt="description of image"
                        className = "center"
                        style = {{width:"45%"}}
                    />
                </Modal.Body>
            </div>,


            //Page 3
            <div>
                <Modal.Body>
                    You made a mistake? No problem!  
                </Modal.Body>
                <Modal.Body>
                    Just remove the wrong number by dragging it in the trash!
                </Modal.Body>
                <Modal.Body>
                    <img 
                        src = {deleteGif}
                        alt="description of image"
                        className = "center"
                        style = {{width:"45%"}}
                    />
                </Modal.Body>
            </div>,

            //Page 4
            <div>
                <Modal.Body>
                    You tired to play? Choose a solver algorithm  and visualize it!
                </Modal.Body>
                <Modal.Body>
                    <img 
                        src = {PickAlgorithmImage}
                        alt="description of image"
                        style = {{width: "30%"}}
                        className = "center"
                    />
                </Modal.Body>
                <Modal.Body>
                    Don't worry, a brief description of each Algo is given in the next page ;)
                </Modal.Body>
            </div>,

            //Page 5
            <div>
                <Accordion>
                  <Card>
                    <Card.Header>
                      <Accordion.Toggle as={Button} variant="link" eventKey="0">
                        Classic backtrack
                      </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="0">
                        <Card.Body> 
                            <p>
                                In this algorithm, we iterate through the grid from top to bottom and left to right. 
                                For each cell, it starts iterating through the numbers that fit in this cell and moves on to the next cell.
                                If at certain point, the grid cannot be filled anymore, it backtracks to the previous cell and test the next number.
                            </p>
                            <p>
                                This algo is relatively slow, and will be improved in the next two approaches.
                            </p>
                        </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                  <Card>
                    <Card.Header>
                      <Accordion.Toggle as={Button} variant="link" eventKey="1">
                        Greedy Backtracking
                      </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="1">
                        <Card.Body> 
                            <p>
                                The Greedy Backtrack is similar to the "classic" one, except for one point: At each step, 
                                it determines the cell with the minimum number of possible candidates.
                                Once this is done, it starts iterating on the possible numbers of this cell, and backtrack when necessary.
                            </p>
                            <p>
                                The greedy approach induce a significative gain of time
                            </p>
                        </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                  <Card>
                    <Card.Header>
                      <Accordion.Toggle as={Button} variant="link" eventKey="2">
                        Heuristics + BackTrack
                      </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="2">
                      <Card.Body> By far the fastest Algorithm! 
                        <p>
                            This algorithm use two parts: At first, it uses three different heuristics in order to reduce the number of possible candidates in each cell.
                            Then, it starts the backctracking in a greedy way.
                            Every time a number is tested in a cell, all the three heusristics are re-applied. 
                            This operation is repeted until the gride is completely filled.
                        </p>
                        <p>
                            Here again, the heuristics that are used will be presented in the next page.
                        </p>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                </Accordion>
            </div>, 

            // Page 6
            <div>
                <Modal.Body>
                    The three heuristics that we used are the following:  
                </Modal.Body>
                <Accordion>
                  <Card>
                    <Card.Header>
                      <Accordion.Toggle as={Button} variant="link" eventKey="0">
                        Lone number
                      </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="0">
                        <Card.Body> 
                            <p>
                                If a number is consistent in only one cell of a row, column or box, then this number is the right one. 
                            </p>
                             <img 
                                src = {loneNumberEx}
                                alt="description of image"
                                style = {{width: "30%"}}
                                className = "center"
                            />
                            <p>
                                In this example, 2 is the right number in the last cell, since it doesn't fit anywhere else in the row. 
                            </p>
                            
                        </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                  <Card>
                    <Card.Header>
                      <Accordion.Toggle as={Button} variant="link" eventKey="1">
                        Naked subset
                      </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="1">
                        <Card.Body> 
                            <p>
                                If k cells contain the same k candidates, then, those k candidtates can be removed from the other cells.
                            </p>
                            <img 
                                src = {nakedSubsetEx}
                                alt="description of image"
                                style = {{width: "65%"}}
                                className = "center"
                            />
                            <p>
                                Here, the red numbers 5,6 and 8 can be removed from the other cells.
                            <img 
                                src = {nakedSubsetSolved}
                                alt="description of image"
                                style = {{width: "65%"}}
                                className = "center"
                            />
                            </p>
                        </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                  <Card>
                    <Card.Header>
                      <Accordion.Toggle as={Button} variant="link" eventKey="2">
                        Disjoint chain
                      </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="2">
                      <Card.Body> By far the fastest Algorithm! 
                        <p>
                            If exactly k cells contain a subset of k candidates, then, those k candidtates can be removed from the other cells.
                        </p>
                        <img 
                            src = {disjointChainEx}
                            alt="description of image"
                            style = {{width: "65%"}}
                            className = "center"
                        />
                        <p>
                            Here, the red numbers 5,6 and 7 can be removed from the other cells.
                        </p>
                        <img 
                            src = {disjointChainSolved}
                            alt="description of image"
                            style = {{width: "65%"}}
                            className = "center"
                        />
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                </Accordion>
            </div>,


            // Page 7
            <div>
                <Modal.Body>
                    Use the navbar to test a bunch of amazing fonctionalities! ;)  
                </Modal.Body>
                <Modal.Body>
                    You can generate a new gride, solve it, empty it once you solved it.
                    You can also activate a "help mode", that shows you the available numbers in each cell
                </Modal.Body>
                <Modal.Body>
                    <img 
                        src = {navBarPicture}
                        alt="description of image"
                        className = "center"
                        style = {{width:"95%", height: "30%"}}
                    />
                </Modal.Body>
            </div>,

            // Page 8
            <div>
                <Modal.Body>
                    Hope you'll master the art of Sudoku and have great time using this app!! 
                </Modal.Body>
                <Modal.Body>
                    Thanks for your time! 
                </Modal.Body>
            </div>
        ]
    const [currentPage, setCurrentPage] = useState(0)

    const handleClose = () => {
        setCurrentPage(0)
        props.setTuto(false)
    }

    const handleShow = () => props.setTuto(true)

    const nextPage = () => {
        if (currentPage< titles.length-1){
            setCurrentPage(currentPage +1)
        } else {
            handleClose()
        }
    }

    const prevPage = () => {
        
        if (currentPage > 0){
            setCurrentPage(currentPage -1)
        } 
    }

    const nextInfo = currentPage< titles.length-1 ? ("Next") : ("Close")
    const disabledInfo = currentPage === 0? (true) : (false)
    
    return (
        <div>
            <Modal 
                show={props.showTuto}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
                centered
                size = 'lg'
                className = "myModal"
                >
                    <Modal.Header closeButton >
                        <Modal.Title style = {{fontWeight:"bold", fontSize: "3.2vh"}}> {titles[currentPage]} </Modal.Title>
                    </Modal.Header>
                    {bodyText[currentPage]}
                    <Modal.Footer className = "modal-footer">
                        <div className = "page-number"> {currentPage+1} / {titles.length}</div>
                        <Button variant= "outline-info" onClick={handleClose}>
                            Skip Tutorial
                        </Button>
                        <Button variant = "primary" onClick = {prevPage} disabled = {disabledInfo}> Previous </Button>
                        <Button variant = "primary" onClick = {nextPage}> {nextInfo} </Button>
                        
                    </Modal.Footer>
            </Modal>
        </div>
    );
}
